'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory} from 'react-router';

var appBasePath=globalData.appBasePath;
var SetPsd=React.createClass({
	getInitialState:function(){
		return {
			
		}
	},
	vauleChange:function(e){
		this.setState({
			[e.target.name]:  e.target.value
		})
	},
	psdLogin:function(){
		var that=this;
		let psd=that.state.psd;
		let surePsd=that.state.surePsd;
		var toast = new Toast();
		if(psd!==surePsd){
			toast.show("");
		}else{
			let path = {
			  pathname:'/'
			}
			hashHistory.push(path);
		}
		
	},
	render:function(){
		var that=this;
		var phoneNum=this.props.location.state.phoneNum;
       return (
        	<div className="setPsd app_Box">
        		<Header title="" />
        		<div className="setPsdCon">
        			<div className="inputPsd">
    					<label htmlFor="psd"></label>
    					<input id="psd"  type="password"  name="psd" placeholder="" onChange={that.vauleChange}/>
    				</div>
    				<div className="inputPsd">
    					<label htmlFor="surePsd"></label>
    					<input id="surePsd"  type="password"  name="surePsd" placeholder="" onChange={that.vauleChange}/>
    				</div>
    				<div className="psdLogin" onClick={that.psdLogin}></div>
        		</div>
        	</div>
        )
	}
	
});


export default SetPsd;





