'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';

var toast = new Toast();
var appBasePath=globalData.appBasePath;
var ApplyLevel=React.createClass({
	getInitialState:function(){
		return {
			checked:true
		}
	},
	
	componentWillMount:function(){
		
	},
	toBack:function(){
		const backRouter = this.props.backRouter;
        if (backRouter) {
            hashHistory.push(backRouter);
        } else {
            window.history.back()
        }
	},
	toApplyResult:function(){
		if(!this.state.checked){
			toast.show("",2000);
		}else{
			var data = {id:3,name:"qin",age:18};
			var path = {
			  pathname:'/ApplyResult',
			  state:data,
			}
			hashHistory.push(path);
		}
	},
	agreeRule:function(event){
		console.log(event.target.checked);
		this.setState({
			checked:event.target.checked
		})
	},
	
	render:function(){
		var that=this;
		//console.log("cityId",cityId);
        return (
        	<div className="app_Box applyFlow">
      			<div className="header">
	        		<div className="toBack" onClick={that.toBack}><img src="src/img/icon/backWhite.png"/></div>
		        	<p className="title"></p>
		        	<div className="headerLinkBtn"></div>
	        	</div>
	        	<div className="applyLevelCon content">
					<ul className="stepBox">
						<li>
							<h1 className="stepActive">1</h1>
							<p></p>
						</li>
						<span></span>
						<li>
							<h1 className="stepActive">2</h1>
							<p></p>
						</li>
						<span></span>
						<li>
							<h1>3</h1>
							<p></p>
						</li>
					</ul>
					<form className="applyLevel">
						<ul>
							<li className="levelInfo">
								<label htmlFor="job"></label>
								<input type="text" id="job" readOnly="readonly"  placeholder=""/>
								<ul>
									
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="publicMoney"></label>
								<input type="text" id="publicMoney" readOnly="readonly" placeholder=""/>
								<ul>
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="social"></label>
								<input type="text" id="social" readOnly="readonly"  placeholder=""/>
								<ul>
									
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="house"></label>
								<input type="text" id="house" readOnly="readonly"  placeholder=""/>
								<ul>
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="car"></label>
								<input type="text" id="car" readOnly="readonly"  placeholder=""/>
								<ul>
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="credit"></label>
								<input type="text" id="credit" readOnly="readonly"  placeholder=""/>
								<ul>
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="age"></label>
								<span></span>
								<input type="text" id="age"   placeholder=""/>
								
								<ul>
								</ul>
							</li>
							<li className="levelInfo">
								<label htmlFor="history"></label>
								<input type="text" id="history" readOnly="readonly"  placeholder=""/>
								<ul>
								</ul>
							</li>
						</ul>
						
						
					</form>
					
					<div className="rule">
						<input className="magic-checkbox" type="checkbox"  id="ruleCheck" checked={that.state.checked}  onChange={that.agreeRule}/>
						<label htmlFor="ruleCheck"></label>
						<Link to={   
						         {   
						             pathname:"/txt",   
						             //hash:'#ahash',    
						             state:{title: '',backRouter:'/Login'}    
						         }   
						    } >
    					
						</Link>   
						
					</div>
	        	</div>	
	        	<div className="botBtn" onClick={that.toApplyResult}></div>
        	</div>
        )
	}
});


export default ApplyLevel;





