'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import HomeHeader from './homeHeader';
import Footer from './footer';
import { hashHistory, Link } from 'react-router';
import '../css/home.css';

var appBasePath=globalData.appBasePath;
var Home=React.createClass({
	getInitialState:function(){
		return {
			activeTab: 1,
			isShow: false,
			activeIndex:0
		}
	},
	
	componentWillMount:function(){
		
	},
	
	toNewsDetail:function(){
		var data = {id:3,name:"qin",age:18};
		var path = {
		  pathname:'/NewsDetail',
		  query:data,
		}
		hashHistory.push(path);
	},
	toListDetail:function(){
		var data = {id:3,name:"qin",age:18};
		var path = {
		  pathname:'/ListDetail',
		  query:data,
		}
		hashHistory.push(path);
	},
	toList:function(e){
		const listId=e.target.index;
		//const title=e.target.find("p").html();
		const data = {listId:listId,title:"title"};
		const path = {
		  pathname:'/List',
		  state:data,
		}
		hashHistory.push(path);
	},
	render:function(){
		var that=this;
		var curCity=that.props.location.query.cityId;
		
        return (
        	<div className="app_Box home">
      		<HomeHeader curCity={curCity}/>
	        	<div className="content">
	        		<ul className="homeTab">
	        			<li onClick={that.toList}>
	        				<img src="src/img/icon/group.png"/>
	        				<p></p>
	        			</li>
	        			<li onClick={that.toList}>
	        				<img src="src/img/icon/personal.png"/>
	        				<p></p>
	        			</li>
	        			<li onClick={that.toList}>
	        				<img src="src/img/icon/qiye.png"/>
	        				<p></p>
	        			</li>
	        			<li onClick={that.toList}>
	        				<img src="src/img/icon/ziyou.png"/>
	        				<p></p>
	        			</li>
	        		</ul>
	        		<div className="capitalBox">
	        			<div className="capitalList">
	        				<h3>
	        					<img src="src/img/icon/capitalLogo.jpg"/>
	        					<span></span>
	        				</h3>
	        				<div className="capitalInfo">
	        					<div className="limit">
	        						<h2>500~1000</h2>
	        						<p>()</p>
	        					</div>
	        					<ul className="special">
	        						<li>1</li>
	        						<li>0.3%</li>
	        						<li>7-30</li>
	        					</ul>
	        					<div className="apply">
	        						<a href="javascript:;" onClick={that.toListDetail}></a>
	        					</div>
	        				</div>
	        				<div className="care">
	        					<span></span>
	        					
	        				</div>
	        			</div>
	        			<div className="capitalList">
	        				<h3>
	        					<img src="src/img/icon/capitalLogo.jpg"/>
	        					<span></span>
	        				</h3>
	        				<div className="capitalInfo">
	        					<div className="limit">
	        						<h2>500~1000</h2>
	        						<p>()</p>
	        					</div>
	        					<ul className="special">
	        						<li>1</li>
	        						<li>0.3%</li>
	        						<li>7-30</li>
	        					</ul>
	        					<div className="apply">
	        						<a href="javascript:;" onClick={that.toListDetail}></a>
	        					</div>
	        				</div>
	        				<div className="care">
	        					<span></span>
	        					
	        				</div>
	        			</div>
	        		</div>
	        		<div className="newsBox">
	        				<h3></h3>
	        				<div>
	        					<dl className="newsList" onClick={that.toNewsDetail}>
	        							<dd>
	        								<h4>,?</h4>
	        								<p><span>2017-10-20</span> <span>355</span></p>
	        							</dd>
	        							<dt>
	        								<img src=""/>
	        							</dt>
	        					</dl>
		        				<dl className="newsList" onClick={that.toNewsDetail}>
	        							<dd>
	        								<h4>,?</h4>
	        								<p><span>2017-10-20</span> <span>355</span></p>
	        							</dd>
	        							<dt>
	        								<img src=""/>
	        							</dt>
	        					</dl>
	        				</div>
        					<Link to="/news" className="linkNews"><img src=""/></Link>
	        		</div>
	        		
	        	</div>
				<Footer activeIndex="0"/>
        	</div>
        )
	}
});


export default Home;





