'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';
import '../css/order.css';


var Order=React.createClass({
    getInitialState:function(){
        return {
        	//btnActive: 0 
        }
    },
    toCancel:function(e){
    	e.target.style.backgroundColor = 
    	 e.target.style.backgroundColor === "grey"?"#53a6ff":"grey";
    },
    render:function(){
        var that = this;
        return (
            <div className="app_Box orderList">
                <Header title=""/>
                <div className="orderCon content">
	                <ul>
	                    <li>
	                        <div className="orderNum">
	                            201705092356412
	                            <span></span>
	                        </div>
	                        <h3 className="list_title">
	                            <img src="src/img/icon/order.png" />
	                            <span></span>
	                            <span className="p_name"></span>
	                        </h3>
	                        <ul className="container">
	                            <li> 5000</li>
	                            <li>12</li>
	                            <li>480</li>
	                            <li>3.17%</li>
	                        </ul>
	                        <div className="listFoot">
	                            <span className="status">3</span>
	                            <span onClick={that.toCancel} className='statusBtn'></span>
	                        </div>
	                    </li>
	                    <li>
	                        <div className="orderNum">
	                            201705092356412
	                            <span></span>
	                        </div>
	                        <h3 className="list_title">
	                            <img src="src/img/icon/order.png" />
	                            <span></span>
	                            <span className="p_name"></span>
	                        </h3>
	                        <ul className="container">
	                            <li> 5000</li>
	                            <li>12</li>
	                            <li>480</li>
	                            <li>3.17%</li>
	                        </ul>
	                        <div className="listFoot">
	                            <span className="status">3</span>
	                              <span onClick={that.toCancel} className='statusBtn'></span>
	                        </div>
	                    </li>
	                </ul>
                </div>
            </div>
        )
    }
})



export default Order; 



