'use strict';
import React from 'react';
import ReactDom from 'react-dom';
import api from './api';
import {globalData} from './global.js';
import Header from './header';
import { hashHistory, Link } from 'react-router';
import '../css/login.css';
var toast = new Toast();
var appBasePath=globalData.appBasePath;
var Login=React.createClass({
	getInitialState:function(){
		var eyeImg=[<img src="src/img/icon/by.png" key={"by"}/>];
		//eyeImg.push('<img src="src/img/icon/by.png" />');
		return {
			wayNum:1,
			eyeImg:eyeImg,
			inputType:"password",
			changePhoneTxt:"",
			getMsg:{
				style:{
						backgroundColor:"#ffa81e",
						color:"#ffffff"
					},
				getMsgTxt:"",
				disabled:false
			},
			display:{
				display:"none"
			}
		}
	},
	checkWay:function(e){
		var id=e.target.id*1;
		this.setState({
			wayNum:id
			//loadingstate:2
		});
		switch (id){
			case 1:
				$("#psdBox").show();
				$("#yzBox").hide();
				break;
			case 2:
				$("#psdBox").hide();
				$("#yzBox").show();
				break;
			default:
				break;
		}
		
	},
	submitHandler:function(){
		var that=this;
		var wayNum=this.state.wayNum;
		var phoneNum=that.state.phoneNum;
		if(!(/^1[34578]\d{9}$/.test(phoneNum))){
/*			this.setState({
				selecthint:1,
				hint:""
			})
			setTimeout(() => {
                this.setState({
					selecthint:0				
				})
            }, 2000);*/
           toast.show("",2000);
		}else{
			switch (wayNum){
				case 1:
					var psd=that.state.password;
					if(psd==""||psd==null){
						 toast.show("",2000);
					}else{
						toast.show("login",2000);
					}
					break;
				case 2:
					var yzCode=that.state.yzCode;
					if(yzCode==""||yzCode==null){
						 toast.show("",2000);
					}else{
						console.log("login");
						var data = {phoneNum:phoneNum};
						var path = {
						  pathname:'/setPsd',
						  state:data,
						}
						hashHistory.push(path);
					}
					break
				default:
					break;
			}
			
		}
	},
	
	
	componentDidMount:function(){
		var that=this;
		/*api.queryBanner(function(data){
				console.log(data);
				if(data.result=="000000"){
					that.setState({
						isShow: true,
		                
		               // dataStatus: 0
		            },()=>{
		               
		            });
				}else{
					
				}
				
			});*/
		    
	},
	eyesHandle:function(){
		var type=$("#psd")[0].type;
		var eyeImg=[];
		if(type=="password"){
			eyeImg.push(<img src="src/img/icon/zy.png" key={"zy"}/>);
			this.setState({
				inputType:"text",
				eyeImg:eyeImg
			})
			
		}else{
			eyeImg.push(<img src="src/img/icon/by.png" key={"by"}/>);
			this.setState({
				inputType:"password",
				eyeImg:eyeImg
			})
		}
	},
	
	changeMsgTxt:function(e){
		this.setState({
			getMsg: {
				getMsgTxt: e.target.value
			}
		})
	},
	
	changeInputTxt:function(e){
		this.setState({
			[e.target.name]:e.target.value
		})
	},
	getMsg:function(){
		
		var that=this;
		//inputdisablereadonlyonClickiOSonTouchEndAndroidHackiosonTouchEndandroidonClick
		let phoneNum=that.state.phoneNum;
		if(!(/^1[34578]\d{9}$/.test(phoneNum))){
			toast.show("",2000);
		}else{
			setTimeout(function(){
				var time=11;
				var timeevt=setInterval(function(){
						time--;
						that.setState({
							getMsg:{
								style:{
										backgroundColor:"#aaaaaa",
										color:"#ffffff"
									},
								getMsgTxt:time+"s",
								disabled:true
							}
						})
						
						if(time==0){
							clearInterval(timeevt);
							that.setState({
								getMsg:{
									style:{
											backgroundColor:"#ffa81e",
											color:"#ffffff"
										},
								getMsgTxt:"",
								disabled:false
								}
							})
						}
					},1000)
			},1000);
		}
	},
	render:function(){
		var that=this;
		let backRouter=that.props.params.backRouter;
		let getMsgStyle=that.state.getMsg.style;
		let display=that.state.display;
		let getMsgTxt=that.state.getMsg.getMsgTxt;
		let disabled=that.state.getMsg.disabled;
		let changeMsgTxt=that.state.changeMsgTxt;
        return (
        	<div className="login">
        		<Header title="" backRouter={backRouter}/>
        		<div className="loginCon">
        			<div className="loginWay">
        				<span className={that.state.wayNum==1?"wayActive":''} onClick={that.checkWay} id="1"></span>
        				<span className={that.state.wayNum==2?"wayActive":''} onClick={that.checkWay} id="2"></span>
        			</div>
        			<div className="infoBox">
        				<form>
	        				<div className="inputLine">
	        					<label htmlFor="phoneNum"></label>
	        					<input id="phoneNum"  type="text" onChange={that.changeInputTxt} className="flex1" name="phoneNum" placeholder=""/>
	        				</div>
	        				<div className="inputLine" id="psdBox">
	        					<label htmlFor="psd"></label>
	        					<span className="flex1 psdInput"><input id="psd" type={that.state.inputType} onChange={that.changeInputTxt} name="password" placeholder="" /></span>
	      						<span className="eyes" id="eyes" onClick={that.eyesHandle}>
	      							{that.state.eyeImg}
	      						</span>
	        				</div>
	        				<div  className="inputLine" id="yzBox" style={display}>
	        					<label htmlFor="yzCode"></label>
	        					<input className="flex1" id="yzCode" type="text" name="yzCode" placeholder=""  onChange={that.changeInputTxt} />
	      						<input type="text" onClick={that.getMsg} placeholder="" readOnly="readOnly" disabled={disabled} style={getMsgStyle} className="getMsg" id="getMsg"  value={getMsgTxt} onChange={that.changeMsgTxt}/>
	        				</div>
	        			
	        				<a className="loginBtn" onClick={that.submitHandler}></a>
        				</form>
        				<p className="note">
        					<span></span>
        					<Link to={   
							         {   
							             pathname:"/txt",   
							             //hash:'#ahash',    
							             state:{title: '',backRouter:'/Login'}    
							             //state:{data:'hello'}     
							         }   
							    } >
        					
							</Link>   
        				</p>
        			</div>
        		</div>
        	</div>
        )
	}
	
});

export default Login;





